  src: url("../fonts/open-sans-v20-latin-regular.woff2") format("woff2"), url("../fonts/open-sans-v20-latin-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/open-sans-v20-latin-700.woff2") format("woff2"), url("../fonts/open-sans-v20-latin-700.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/open-sans-v20-latin-300.woff2") format("woff2"), url("../fonts/open-sans-v20-latin-300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.page {
  font-family: "Open Sans", "Arial", sans-serif;
  height: 100%;
  margin: 0;
  padding: 0; }

.body {
  overflow: visible;
  margin: 0;
  padding: 0;
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr -webkit-min-content;
  grid-template-rows: 1fr min-content;
  -ms-flex-line-pack: start;
      align-content: start; }

.body--noscroll {
  overflow: hidden; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }
